</template>

<script>
import AddressModal from './AddressModal.vue';
import logo from '@/assets/logo.jpg'

export default {
  name: 'NavBar',
  components: {
    AddressModal
  },
  data() {
    return {
      location: '',
      userName: '',
      isLoggedIn: false,
      logo: logo
    };
  },
  methods: {
    changeLocation() {
      const newLocation = prompt('', this.location);
      if (newLocation !== null && newLocation.trim() !== '') {
        this.location = newLocation.trim();
      }
    },
    updateLocation(newAddress) {
      this.location = newAddress;
    },
    logout() {
      // Implement logout logic here
      this.userName = '';
      this.isLoggedIn = false;
      // Optionally, redirect to login page
      this.$router.push('/login');
    },
    async fetchUser() {
      try {
        // Simulate API call
        const response = await fetch('/api/user'); // Replace with your actual API endpoint
        if (response.ok) {
          const user = await response.json();
          this.userName = user.name;
          this.isLoggedIn = user.loggedIn;
        } else {
          console.error('Failed to fetch user data');
        }
      } catch (error) {
        console.error('Error fetching user data:', error);
      }
    }
  },
  created() {
    this.fetchUser();
  }
};
</script>

